@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  font-family: 'Rubik', sans-serif;
  margin: 0;
  background-color:white;
}


.expense-item {
    background-color: #FAF9FB;
    border-radius: 20px;
}

.expense-title {
    font-size: 1.4rem;
}

.expense-amount {
    font-size: 1.2rem;
    font-weight: lighter;
}

.expense-category {
    background-color: #F7F4F7 !important;
    color: #b79393;
}

.expenses-list__fallback {
    text-align: center;
}
.expense-date {
  font-size: .95rem;
  color: #8B718A;
}
.expenses .chart .bar:nth-child(odd) .fill {
  background-color: #684E6E;
}

.expenses .chart .bar:nth-child(even) .fill {
  background-color: #55385C;
}

.expenses .chart .bar, .expenses .chart .bar .fill {
  border-radius: 25px;
}

.expenses .chart .bar {
  height: 100%;
  margin:0 5px;
  padding: 0;
  color:#A08FA4;
  text-align: center;
}

@media (max-width: 768px) {
  .expenses .chart .bar .fill {
      width: 25px;
  }

  .chart {
      overflow-y: scroll;
  }
}

@media (min-width: 768px) {
  .expenses .chart .bar {
      margin: 0 10px;
  }
}

@media (min-width:992px) {
  .expenses .chart .bar {
      margin: 0 18px;
  }
}
.expenses .chart {
  height: 320px;
  padding: 55px 23px;
  background-color: #42224A;
  border-radius: 40px;
  align-items: flex-end;   
}

@media (max-width: 768px) {
  .chart {
      overflow-y: scroll;
  }
}
.new-expense__controls {
    background-color: #FAF9FB;
    border-radius: 40px;
}

.expense-controls__btn {
    background-color: #B6588B !important;
    box-shadow: 0px 6px 4px rgba(0, 0, 0, 0.25);
    border: none;
    color: white !important;
    border-radius: 20px !important;
    padding: 8px 30px !important;
}


